.features {
  padding: 32px 0;
}
.features__list {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-gap: 24px;
}
@media (max-width: 800px) {
  .features__list {
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (max-width: 576px) {
  .features__list {
    grid-gap: 16px;
  }
}
@media (max-width: 480px) {
  .features__list {
    grid-template-columns: repeat(1, 1fr);
  }
}
.features__item {
  background: #ffffff;
  border: 1px solid var(--color-border-dark);
  border-radius: 6px;
  padding: 24px;
}
.features__icon {
  display: inline-block;
  margin-bottom: 16px;
}
.features__text {
  font-weight: 16px;
  font-weight: 500;
  line-height: 1.4;
  margin: 0;
}

@media (max-width: 576px) {
  .features__item {
    padding: 16px;
  }
  .features__text {
    font-weight: 400;
  }
}
@media (max-width: 480px) {
  .features__item {
    display: flex;
    align-items: center;
  }
  .features__icon {
    flex-shrink: 0;
    width: 48px;
    height: 48px;
    margin-right: 16px;
  }
  .features__text {
    font-size: 14px;
    line-height: 20px;
  }
}