.features {
  padding: 32px 0;
  &__list {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-gap: 24px;

    @media (max-width: 800px) {
      grid-template-columns: repeat(2, 1fr);
    }

    @media (max-width: 576px) {
      grid-gap: 16px;
    }

    @media (max-width: 480px) {
      grid-template-columns: repeat(1, 1fr);
    }
  }
  &__item {
    background: #ffffff;
    border: 1px solid var(--color-border-dark);
    border-radius: 6px;
    padding: 24px;
  }
  &__icon {
    display: inline-block;
    margin-bottom: 16px;
  }
  &__text {
    font-weight: 16px;
    font-weight: 500;
    line-height: 1.4;
    margin: 0;
  }
}

@media (max-width: 576px) {
  .features {
    &__item {
      padding: 16px;
    }

    &__text {
      font-weight: 400;
    }
  }
}

@media (max-width: 480px) {
  .features__item {
    display: flex;
    align-items: center;
  }

  .features__icon {
    flex-shrink: 0;
    width: 48px;
    height: 48px;
    margin-right: 16px;
  }

  .features__text {
    font-size: 14px;
    line-height: 20px;
  }
}
